'use client'

import { useEffect, useState } from 'react'
import { useParams, useRouter } from 'next/navigation'
import { supabase } from '@/lib/supabase'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { 
  ArrowLeft,
  FileText,
  CheckCircle,
  AlertTriangle,
  Clock,
  TrendingUp,
  Calendar,
  User
} from 'lucide-react'

interface Project {
  id: string
  name: string
  description: string
  created_at: string
  created_by: string
  profiles?: {
    full_name: string
    email: string
  }
}

interface RAMSDocument {
  id: string
  title: string
  description: string
  status: 'draft' | 'submitted' | 'approved' | 'rejected' | 'pending_review'
  created_at: string
  updated_at: string
  created_by: string
  profiles?: {
    full_name: string
    email: string
  }
}

interface RAMSStats {
  total: number
  draft: number
  submitted: number
  approved: number
  rejected: number
  pending_review: number
}

export default function ProjectDetailsPage() {
  const params = useParams()
  const router = useRouter()
  const projectId = params.id as string
  
  const [project, setProject] = useState<Project | null>(null)
  const [ramsDocuments, setRamsDocuments] = useState<RAMSDocument[]>([])
  const [ramsStats, setRamsStats] = useState<RAMSStats>({
    total: 0,
    draft: 0,
    submitted: 0,
    approved: 0,
    rejected: 0,
    pending_review: 0
  })
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const loadProjectDetails = async () => {
      try {
        setError(null)
        
        // Get project details
        const { data: projectData, error: projectError } = await supabase
          .from('projects')
          .select('*')
          .eq('id', projectId)
          .single()
        
        if (projectError) {
          console.error('Error loading project:', projectError)
          setError(projectError.message)
          return
        }

        // Get profile information
        const { data: profileData, error: profileError } = await supabase
          .from('profiles')
          .select('id, full_name, email')
          .eq('id', projectData.created_by)
          .single()
        
        if (profileError) {
          console.error('Error loading profile:', profileError)
        }

        setProject({
          ...projectData,
          profiles: profileData
        })
      } catch (err) {
        console.error('Unexpected error:', err)
        setError('Failed to load project details')
      }
    }

    const loadRAMSStats = async () => {
      try {
        // Get RAMS associated with this project
        const { data: ramsData, error: ramsError } = await supabase
          .from('rams')
          .select(`
            id,
            title,
            description,
            status,
            created_at,
            updated_at,
            created_by,
            profiles:created_by (
              id,
              full_name,
              email
            )
          `)
          .eq('project_id', projectId)
          .order('created_at', { ascending: false })
        
        if (ramsError) {
          // Check if the error is because the table doesn't exist
          if (ramsError.code === 'PGRST116' || ramsError.message.includes('does not exist')) {
            console.log('RAMS table does not exist yet, using default stats')
            setRamsStats({
              total: 0,
              draft: 0,
              submitted: 0,
              approved: 0,
              rejected: 0,
              pending_review: 0
            })
            setRamsDocuments([])
            setLoading(false)
            return
          }
          
          console.error('Error loading RAMS stats:', ramsError)
          setLoading(false)
          return
        }

        // Set the documents with proper type mapping
        const mappedDocuments: RAMSDocument[] = (ramsData || []).map(ram => ({
          ...ram,
          profiles: Array.isArray(ram.profiles) && ram.profiles.length > 0 ? ram.profiles[0] : undefined
        }))
        setRamsDocuments(mappedDocuments)

        // Calculate statistics
        const stats: RAMSStats = {
          total: ramsData?.length || 0,
          draft: 0,
          submitted: 0,
          approved: 0,
          rejected: 0,
          pending_review: 0
        }

        ramsData?.forEach(ram => {
          switch (ram.status) {
            case 'draft':
              stats.draft++
              break
            case 'submitted':
              stats.submitted++
              break
            case 'approved':
              stats.approved++
              break
            case 'rejected':
              stats.rejected++
              break
            case 'pending_review':
              stats.pending_review++
              break
          }
        })

        setRamsStats(stats)
      } catch {
        console.log('RAMS functionality not yet implemented, using default stats')
        setRamsStats({
          total: 0,
          draft: 0,
          submitted: 0,
          approved: 0,
          rejected: 0,
          pending_review: 0
        })
        setRamsDocuments([])
      }
      
      setLoading(false)
    }

    const loadData = async () => {
      if (projectId) {
        await loadProjectDetails()
        await loadRAMSStats()
      }
    }
    
    loadData()
  }, [projectId])

  if (loading) {
    return <div className="p-8">Loading project details...</div>
  }

  if (error) {
    return (
      <div className="p-8">
        <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg">
          <p className="text-red-800 text-sm">Error: {error}</p>
          <Button 
            onClick={() => router.back()} 
            variant="outline" 
            size="sm" 
            className="mt-2"
          >
            Go Back
          </Button>
        </div>
      </div>
    )
  }

  if (!project) {
    return (
      <div className="p-8">
        <div className="text-center">
          <h3 className="text-lg font-medium text-gray-900 mb-2">Project not found</h3>
          <p className="text-gray-500 mb-4">The project you&apos;re looking for doesn&apos;t exist.</p>
          <Button onClick={() => router.push('/dashboard/projects')}>
            Back to Projects
          </Button>
        </div>
      </div>
    )
  }

  return (
    <div className="p-8 max-w-7xl mx-auto">
      {/* Header */}
      <div className="mb-8">
        <div className="flex items-center gap-4 mb-4">
          <Button 
            variant="ghost" 
            size="sm" 
            onClick={() => router.push('/dashboard/projects')}
            className="flex items-center gap-2"
          >
            <ArrowLeft className="w-4 h-4" />
            Back to Projects
          </Button>
        </div>
        
        <div className="flex items-center gap-4 mb-4">
          <div className="w-12 h-12 bg-blue-100 rounded-full flex items-center justify-center text-blue-600 font-medium text-lg">
            {project.name.slice(0, 2).toUpperCase()}
          </div>
          <div>
            <h1 className="text-2xl font-semibold text-gray-900">{project.name}</h1>
            <p className="text-gray-600">{project.description || 'No description provided'}</p>
          </div>
        </div>

        {/* Project Meta Info */}
        <div className="flex items-center gap-6 text-sm text-gray-500">
          <div className="flex items-center gap-2">
            <User className="w-4 h-4" />
            <span>Created by {project.profiles?.full_name || 'Unknown User'}</span>
          </div>
          <div className="flex items-center gap-2">
            <Calendar className="w-4 h-4" />
            <span>Created {new Date(project.created_at).toLocaleDateString('en-US', {
              month: 'long',
              day: 'numeric',
              year: 'numeric'
            })}</span>
          </div>
        </div>
      </div>

      {/* RAMS Statistics */}
      <div className="mb-8">
        <h2 className="text-lg font-semibold text-gray-900 mb-4">RAMS Overview</h2>
        
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-6 gap-4 mb-6">
          {/* Total RAMS */}
          <Card>
            <CardContent className="p-4">
              <div className="flex items-center space-x-2">
                <FileText className="w-5 h-5 text-blue-600" />
                <div>
                  <p className="text-2xl font-semibold text-gray-900">{ramsStats.total}</p>
                  <p className="text-sm text-gray-600">Total RAMS</p>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Draft */}
          <Card>
            <CardContent className="p-4">
              <div className="flex items-center space-x-2">
                <Clock className="w-5 h-5 text-gray-500" />
                <div>
                  <p className="text-2xl font-semibold text-gray-900">{ramsStats.draft}</p>
                  <p className="text-sm text-gray-600">Draft</p>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Submitted */}
          <Card>
            <CardContent className="p-4">
              <div className="flex items-center space-x-2">
                <TrendingUp className="w-5 h-5 text-blue-600" />
                <div>
                  <p className="text-2xl font-semibold text-gray-900">{ramsStats.submitted}</p>
                  <p className="text-sm text-gray-600">Submitted</p>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Pending Review */}
          <Card>
            <CardContent className="p-4">
              <div className="flex items-center space-x-2">
                <AlertTriangle className="w-5 h-5 text-yellow-600" />
                <div>
                  <p className="text-2xl font-semibold text-gray-900">{ramsStats.pending_review}</p>
                  <p className="text-sm text-gray-600">Pending Review</p>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Approved */}
          <Card>
            <CardContent className="p-4">
              <div className="flex items-center space-x-2">
                <CheckCircle className="w-5 h-5 text-green-600" />
                <div>
                  <p className="text-2xl font-semibold text-gray-900">{ramsStats.approved}</p>
                  <p className="text-sm text-gray-600">Approved</p>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Rejected */}
          <Card>
            <CardContent className="p-4">
              <div className="flex items-center space-x-2">
                <AlertTriangle className="w-5 h-5 text-red-600" />
                <div>
                  <p className="text-2xl font-semibold text-gray-900">{ramsStats.rejected}</p>
                  <p className="text-sm text-gray-600">Rejected</p>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>

      {/* Quick Actions */}
      <div className="mb-8">
        <h2 className="text-lg font-semibold text-gray-900 mb-4">Quick Actions</h2>
        <div className="flex gap-4">
          <Button className="bg-blue-600 hover:bg-blue-700 text-white">
            Create New RAMS
          </Button>
          <Button variant="outline">
            View All RAMS
          </Button>
          <Button variant="outline">
            Export Report
          </Button>
        </div>
      </div>

      {/* Recent Activity */}
      <Card>
        <CardHeader>
          <CardTitle>Recent RAMS Activity</CardTitle>
        </CardHeader>
        <CardContent>
          {ramsStats.total === 0 ? (
            <div className="text-center py-8">
              <FileText className="w-12 h-12 text-gray-400 mx-auto mb-4" />
              <h3 className="text-lg font-medium text-gray-900 mb-2">No RAMS yet</h3>
              <p className="text-gray-500 mb-4">Create your first RAMS document to get started</p>
              <Button className="bg-blue-600 hover:bg-blue-700 text-white">
                Create RAMS
              </Button>
            </div>
          ) : (
            <p className="text-gray-500">RAMS activity will be shown here when available.</p>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
